<template>
	<section> 
		<el-form class="toolbar" label-width="120px" style="margin-top:10px;padding-top:20px;">
			<el-row>
				<el-col :span="8">
					<el-form-item label="">
						<el-date-picker
                              v-model="dateValue"
                              type="daterange"
							  @change="datechange"
							  style="width:100%"
                              range-separator=""
                              value-format="yyyy-MM-dd"
                              start-placeholder=""
                              end-placeholder="">
                        </el-date-picker>
					</el-form-item> 
				</el-col>
				<el-col :span="8">
					<el-form-item label="">
							<el-input v-model="account"></el-input>
					</el-form-item> 
				</el-col>	
				<el-col :span="8">
					<el-form-item label="">
							<el-input v-model="orderId"></el-input>
					</el-form-item> 
				</el-col>
				
				
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item label="">
							<el-input v-model="orderNum"></el-input>
					</el-form-item> 
				</el-col>
				<el-col :span="6">
					<el-form-item label="">
						  <el-select v-model="webSiteId" placeholder="">
							<el-option
							v-for="item in fzlist"
							:key="item.id"
							:label="item.name"
							:value="item.id">
							</el-option>
						</el-select>
					</el-form-item> 
				</el-col>	
				<el-col :span="5">
					<el-form-item label="">
							<el-select v-model="orignal" placeholder="">
                                    <el-option label="" value=""></el-option>
                                    <el-option label="" value=""></el-option>
                                    <el-option label="" value=""></el-option> 
                        </el-select>
					</el-form-item> 
				</el-col>	
				<el-col :span="5">
					<el-form-item label="">
						<el-select v-model="state" placeholder="">
                                    <el-option label="" value="0"></el-option>
                                    <el-option label="" value="1"></el-option>
                                </el-select>
					</el-form-item> 
				</el-col>	
			</el-row>
			<el-form-item>
					<el-button type="primary" @click="search"></el-button> 
                    <el-button type="primary" @click="daochu"></el-button> 
                    <el-button type="primary" @click="uploadDiv=true"></el-button> 
			</el-form-item>
		</el-form>

		<!---->
	
			<el-table
          :data="tableData"
          border
          @selection-change="handleSelectionChange"
          :header-cell-style="{background:'#f7f7f7'}"
          style="width: 100%">
          <el-table-column
                                            type="selection"
                                            width="55">
                                          </el-table-column>
          <el-table-column
            prop="webSiteName"
            label=""
            align="center">
          </el-table-column>
		  <el-table-column
            prop="account"
            label=""
            align="center">
          </el-table-column>
          <el-table-column
            prop="orderCount"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            align="center"
            label="">
            <template slot-scope="scope">
                {{scope.row.money/100}} 
            </template>
          </el-table-column>
          <el-table-column
            prop="payTime"
            align="center"
			width="200"
            label="">
          </el-table-column>
		  <el-table-column
            prop="orignal"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="state"
            align="center"
            label="">
             <template slot-scope="scope">
                <span v-if="scope.row.state==0"></span>
                <span v-if="scope.row.state==1"></span>
            </template>
          </el-table-column>
          
          <el-table-column
            label=""
            align="center"
            >
            <template slot-scope="scope">
              <el-button size="mini" @click="detailList(scope.row.id);"></el-button> 
            </template>
          </el-table-column>
        </el-table>

		<div style="padding:20px;text-align:center;">
            <el-pagination
            background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="[10, 20, 30, 40]"
              :page-size="size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>  

	<el-dialog title="" custom-class="orderMain" :visible.sync="detailDiv">
		<el-row :gutter="15">
                         <el-col :span="4"><el-input v-model="detailForm.id" placeholder=""></el-input></el-col>
                         <el-col :span="4"><el-input v-model="detailForm.mobile" placeholder=""></el-input></el-col>
                         <el-col :span="4"><el-input v-model="detailForm.orderNum" placeholder=""></el-input></el-col>
                         <!-- <el-col :span="4">
                           <el-select placeholder="">
                                <el-option label="" value="0"></el-option>
                                <el-option label="" value="1"></el-option>
                          </el-select>
                         </el-col> -->
                         <el-col :span="4"><el-input v-model="detailForm.packNum" placeholder=""></el-input></el-col>
                         <el-col :span="4">
                           <el-date-picker
						    v-model="dateValue"
                              @change="datechange2"
							  placeholder=""
                              type="daterange"
                              range-separator=""
                              value-format="yyyy-MM-dd"
                              start-placeholder=""
                              end-placeholder="">
                            </el-date-picker>
                         </el-col>
						 <el-col :span="4">
                           <el-select v-model="detailForm.state" placeholder="">
                                <el-option label="" value="0"></el-option>
                                <el-option label="" value="1"></el-option>
                          </el-select>
                         </el-col>
                     </el-row>  
                     <div style="padding:15px;text-align:center">
                           <el-button @click="search2" type="primary"></el-button>
                         </div>  
        <el-table
          :data="tableData2"
          border
		  max-height="250"
          style="width: 100%">
          
          <el-table-column
            prop="orderNum"
			fixed
            label=""
            align="center"
            width="200">
          </el-table-column>
          <el-table-column
            prop="expressCompany"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="packNum"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="userName"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            prop="mobile"
            align="center"
            label="">
          </el-table-column>
          <el-table-column
            align="center"
            label="">
             <template slot-scope="scope">
                {{scope.row.price/100}} 
             </template>  
          </el-table-column>
          <el-table-column
            align="center"
            label="">
            <template slot-scope="scope">
                {{scope.row.expressPrice/100}} 
             </template>  
          </el-table-column>
          <el-table-column
            prop="createTime"
            width="200"
            align="center"
            label="">
          </el-table-column>
        </el-table>
		<div style="padding:20px;text-align:center;">
            <el-pagination
            background
              @size-change="handleSizeChange2"
              @current-change="handleCurrentChange2"
              :page-sizes="[10, 20, 30, 40]"
              :page-size="size2"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total2">
            </el-pagination>
          </div>  
    </el-dialog>	

		<el-dialog
		title=""
		:visible.sync="uploadDiv"
		custom-class="upDiv"
		>
			<el-upload
                                    class="upload-demo"
                                    drag
                                    :multiple="false"
                                    :limit=1
                                    :show-file-list="true"
                               :on-change="importExcel"
                               :auto-upload="false"
                                    action="/">
                                    <i class="el-icon-upload"></i>
                                    <div class="el-upload__text"><em></em></div>
                                  </el-upload>
		<span slot="footer" class="dialog-footer">
			<el-button @click="uploadDiv = false"> </el-button>
			<el-button type="primary" @click="uploadDiv = false;uploader()"> </el-button>
		</span>
		</el-dialog>
	</section>
</template>
<script>
import XLSX from 'xlsx'
export default {
  data() {
    return {
	dateValue:'',	
	  account:'',
	  webSiteId:'',
	  orderNum:'',
	  orderId:'',
	  state:'',
	  orignal:'',
      page:1,
      size:10,
      total:0,
	  tableData: [],
	  page2:1,
      size2:10,
      total2:0,
	  tableData2: [],
	  fzlist:[],
	  begin:'',
	  end:'',
	  detailDiv:false,
	  uploadDiv:false,
	  taskIds:'',
	   detailForm:{
        id:'',
        taskId:'',
        mobile:'',
        orderNum:'',
        packNum:'',
        state:'',
        begin:'',
        end:''
	  },
	  list:[]
    };
  },
  mounted(){
	this.getfzlist();  
    this.search();
  },
  methods:{
	  datechange(e){
		 this.begin=e.split('')[0];
		 this.end=e.split('')[1];
	  },
	   datechange2(e){
		 this.detailForm.begin=e.split('')[0];
		 this.detailForm.end=e.split('')[1];
	  },
	getfzlist(){
		let that = this;
      this.axios({
        method: "GET",
        headers: {
          "Authorization": sessionStorage.getItem("token"),
          "Content-Type": "application/x-www-form-urlencoded",
          "domain": "taobao.tianyalei.com"
        },
        url:
          "/webSite?page=0&size=999999&name=&account="
      }).then(function(response) {
        if (response.data.code == 200) {
          that.fzlist = response.data.data.list;
        }
      });
	},
     search(){
      let that = this;
      this.axios
        .get(
          "task?begin="+that.begin+'&end='+that.end+'&state='+that.state+'&page='+(that.page-1)+'&size='+that.size+'&account='+that.account+'&webSiteId='+that.webSiteId+'&orderNum='+that.orderNum+'&orderId='+that.orderId+'&orignal='+that.orignal,
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              "domain": "taobao.tianyalei.com",
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             for(var i=0;i<response.data.data.list.length;i++){
                response.data.data.list[i].payTime=that.gettime(response.data.data.list[i].payTime);
             }
             that.tableData=response.data.data.list;
             that.total=response.data.data.totalCount;
          } 
        });
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.size=val;
      this.search();
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.page=val;
      this.search();
	},
	handleSizeChange2(val) {
      console.log(` ${val} `);
      this.size2=val;
      this.search2();
    },
    handleCurrentChange2(val) {
      console.log(`: ${val}`);
      this.page2=val;
      this.search2();
    },
    gettime(t){
        var _time=new Date(t);
        var   year=_time.getFullYear();//2017
        var   month=_time.getMonth()>8?(_time.getMonth()+1):'0'+(_time.getMonth()+1);//7
        var   date=_time.getDate()>9?_time.getDate():'0'+_time.getDate();//10
        var   hour=_time.getHours()>9?_time.getHours():'0'+_time.getHours();;//10
        var   minute=_time.getMinutes()>9?_time.getMinutes():'0'+_time.getMinutes();;//56
        var   second=_time.getSeconds()>9?_time.getSeconds():'0'+_time.getSeconds();;//15
        
        return   year+"-"+month+"-"+date+"   "+hour+":"+minute+":"+second;//
	},
	detailList(id){
		this.detailDiv=true;
		this.detailForm.taskId=id;
		this.search2();
	},
	search2(){
		this.detailForm.begin=this.dateValue[0];
        this.detailForm.end=this.dateValue[1];
        this.detailForm.page=this.page2-1;
        this.detailForm.size=this.size2;
		let that=this;
       this.axios
          .get(
            '/order?'+that.qs.stringify(that.detailForm),
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                "domain": "taobao.tianyalei.com",
                "Authorization":sessionStorage.getItem('token')
              }
            }
          )
          .then(function(response) {
            if (response.data.code == 200) {
              for(var i=0;i<response.data.data.list.length;i++){
                  if(response.data.data.list[i].createTime){
                      response.data.data.list[i].createTime=that.gettime(response.data.data.list[i].createTime);
                  }
              }
              that.tableData2=response.data.data.list;
              that.total2=response.data.data.totalCount;
            } 
          });
	},
    handleSelectionChange(val) {
        console.log(val)
        let arry=[]
        for(var i=0;i<val.length;i++){
            arry.push(val[i].id)
        }
        this.taskIds=arry.join(',');
	  },
	  daochu(){
        let that=this;
        if(this.taskIds==''){
          that.$message({
              message:"",
              type: 'warning'
           });
           return
        }
        
        this.axios.post('/task/outputExcel',
        this.qs.stringify({taskIds:this.taskIds}),{
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                'domain':'taobao.tianyalei.com',
                "Authorization":sessionStorage.getItem('token')
              }
            })
        .then(function (response) {
            if(response.data.code==200){
               let arry=[];
               for (var i=0;i<response.data.data.length;i++){
                  arry.push({
					  :response.data.data[i].shopName,
                      :response.data.data[i].orderId,
					  :response.data.data[i].userName,
					  :response.data.data[i].mobile,
					  :response.data.data[i].buyAccount,
                      :response.data.data[i].address,
					  :response.data.data[i].expressCompany,
					  :response.data.data[i].condition,
					  :response.data.data[i].shopCode,
					  :response.data.data[i].goodsCount,
					  :response.data.data[i].totalMoney/100,
					  :response.data.data[i].expressMoney/100,
					  :response.data.data[i].youhui/100,
					  :response.data.data[i].goodsMoney/100,
					  :response.data.data[i].orderType
                  })
               }
               that.JSONToExcelConvertor(arry,'')
            }else{
                that.$message({
                  message: response.data.message,
                  type: 'warning'
                });
            }
          })
      },
      //jsonexcel
     JSONToExcelConvertor(JSONData, FileName) {  
            //json  
            var arrData = typeof JSONData != 'object' ? JSON.parse(JSONData) : JSONData;  
            var excel = '<table border="1"  rull="all" style="border-collapse: collapse">';   
            var row = "<tr>";           
            //  
            var keys = Object.keys(JSONData[0]);  
            keys.forEach(function (item) {
                row += "<td>" + item + '</td>';
            });     
            //  
            excel += row + "</tr>";  
            //
            for (var i = 0; i < arrData.length; i++) {  
                var row = "<tr>";  
                for (var index in arrData[i]) {  
                    console.log(arrData[i][index]);
                    //var value = arrData[i][index] === "." ? "" : arrData[i][index];  
                    row += '<td>' + arrData[i][index] + '</td>';  
                }  
                excel += row + "</tr>";  
            }  

            excel += "</table>";  

            var excelFile = "<html xmlns:o='urn:schemas-microsoft-com:office:office' xmlns:x='urn:schemas-microsoft-com:office:excel' xmlns='http://www.w3.org/TR/REC-html40'>";  
            excelFile += '<meta http-equiv="content-type" content="application/vnd.ms-excel; charset=UTF-8">';  
            excelFile += '<meta http-equiv="content-type" content="application/vnd.ms-excel';  
            excelFile += '; charset=UTF-8">';  
            excelFile += "<head>";  
            excelFile += "<!--[if gte mso 9]>";  
            excelFile += "<xml>";  
            excelFile += "<x:ExcelWorkbook>";  
            excelFile += "<x:ExcelWorksheets>";  
            excelFile += "<x:ExcelWorksheet>";  
            excelFile += "<x:Name>";  
            excelFile += "{worksheet}";  
            excelFile += "</x:Name>";  
            excelFile += "<x:WorksheetOptions>";  
            excelFile += "<x:DisplayGridlines/>";  
            excelFile += "</x:WorksheetOptions>";  
            excelFile += "</x:ExcelWorksheet>";  
            excelFile += "</x:ExcelWorksheets>";  
            excelFile += "</x:ExcelWorkbook>";  
            excelFile += "</xml>";  
            excelFile += "<![endif]-->";  
            excelFile += "</head>";  
            excelFile += "<body>";  
            excelFile += excel;  
            excelFile += "</body>";  
            excelFile += "</html>";  

            var uri = 'data:application/vnd.ms-excel;charset=utf-8,' + encodeURIComponent(excelFile);  

            var link = document.createElement("a");      
            link.href = uri;  

            link.style = "visibility:hidden";  
            link.download = FileName + ".xls";  

            document.body.appendChild(link);  
            link.click();  
            document.body.removeChild(link);  
		},
		sortArr(arr, str) {
    var _arr = [],
        _t = [],
        // 
        _tmp;
 
    // 
    arr = arr.sort(function(a, b) {
        var s = a[str],
            t = b[str];
 
        return s < t ? -1 : 1;
    });
 
    if ( arr.length ){
        _tmp = arr[0][str];
    }
    // console.log( arr );
    // 
    for (var i in arr) {
        console.log( _tmp);
        if ( arr[i][str] === _tmp ){
            console.log(_tmp)
            _t.push( arr[i] );
        } else {
            _tmp = arr[i][str];
            _arr.push( _t );
            _t = [arr[i]];
        }
    }
    // 
    _arr.push( _t );
    return _arr;
},
     importExcel(evt) {//
      this.$message({
                  message: '',
                  type: 'success'
                });
      this.buyList=[];          
     let that=this;
	var wb; //
	var rABS = false; //
	var isCSV;
	if(!evt) {
		return;
	}
	var f = evt.raw;
	var reader = new FileReader();
 
	var pet = new Promise(function(resolve, reject) {
		reader.onload = function(e) {
			var data = e.target.result;
			var XLSX = require('xlsx');
			wb = null;
			if(isCSV) {
				data = new Uint8Array(data);
				let f = that.isUTF8(data);
				//console.log("CSV," + (f ? "" : "") + "UTF-8");
				if(f) {
					data = e.target.result;
				} else {
					var str = cptable.utils.decode(936, data);
					wb = XLSX.read(str, {
						type: "string"
					});
				}
			} else {
				//console.log("CSV");
			}
			if(!wb) {
				wb = rABS || isCSV ? XLSX.read(btoa(that.fixdata(data)), {
					type: 'base64'
				}) : XLSX.read(data, {
					type: 'binary'
				});
			}
			//console.log(wb);
			//wb.SheetNames[0]SheetsSheet
			//wb.Sheets[Sheet]Sheet
			//console.log(JSON.parse(JSON.stringify(XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])).replace(/\s+/g,"")));
      var result = JSON.parse(JSON.stringify(XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])).replace(/\s+/g,"").replace(/'/g,''));
      var array=[];
      for(var i=0;i<result.length;i++){
            array.push(
              {
                orderId:result[i]..toString(),
                expressCompany:result[i]..toString(),
                packNum:result[i]..toString()
              }
            )
         }

      that.list=array;
			resolve(result);
		}
		isCSV = f.name.split(".").reverse()[0] == "csv"; // CSV
		if(rABS || isCSV) {
			reader.readAsArrayBuffer(f);
		} else {
			reader.readAsBinaryString(f);
		}
		evt.value = "";
 
	}).catch(error => console.log(error));
	console.log(pet);
	return pet;
},
fixdata(data) { //BinaryString
	var o = "",
		l = 0,
		w = 10240;
	iconv.skipDecodeWarning = true;
	let str = iconv.encode(o, 'utf-8');
	for(; l < data.byteLength / w; ++l) o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)));
	o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)));
	return o;
},
isUTF8(bytes) { //CSVUTF-8
	var i = 0;
	while(i < bytes.length) {
		if(( // ASCII
				bytes[i] == 0x09 ||
				bytes[i] == 0x0A ||
				bytes[i] == 0x0D ||
				(0x20 <= bytes[i] && bytes[i] <= 0x7E)
			)) {
			i += 1;
			continue;
		}
		if(( // non-overlong 2-byte
				(0xC2 <= bytes[i] && bytes[i] <= 0xDF) &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0xBF)
			)) {
			i += 2;
			continue;
		}
		if(( // excluding overlongs
				bytes[i] == 0xE0 &&
				(0xA0 <= bytes[i + 1] && bytes[i + 1] <= 0xBF) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF)
			) ||
			( // straight 3-byte
				((0xE1 <= bytes[i] && bytes[i] <= 0xEC) ||
					bytes[i] == 0xEE ||
					bytes[i] == 0xEF) &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0xBF) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF)
			) ||
			( // excluding surrogates
				bytes[i] == 0xED &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0x9F) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF)
			)
		) {
			i += 3;
			continue;
		}
		if(( // planes 1-3
				bytes[i] == 0xF0 &&
				(0x90 <= bytes[i + 1] && bytes[i + 1] <= 0xBF) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF) &&
				(0x80 <= bytes[i + 3] && bytes[i + 3] <= 0xBF)
			) ||
			( // planes 4-15
				(0xF1 <= bytes[i] && bytes[i] <= 0xF3) &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0xBF) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF) &&
				(0x80 <= bytes[i + 3] && bytes[i + 3] <= 0xBF)
			) ||
			( // plane 16
				bytes[i] == 0xF4 &&
				(0x80 <= bytes[i + 1] && bytes[i + 1] <= 0x8F) &&
				(0x80 <= bytes[i + 2] && bytes[i + 2] <= 0xBF) &&
				(0x80 <= bytes[i + 3] && bytes[i + 3] <= 0xBF)
			)
		) {
			i += 4;
			continue;
		}
		return false;
	}
	return true;
},
	uploader(){
		let that=this; 
					this.axios.post('/task/inputExcel', 
					{
						list:this.list
					}, 
					{
						headers: {
							'Authorization':sessionStorage.getItem("token"),
							'Content-Type':'application/json',
							'domain':'taobao.tianyalei.com'
						}
        })
    .then(function (response) {
       if(response.data.code==200){   
              that.$message({
                message:'',
                type: 'success'
				}); 
			  that.uploadDiv=false;
			  that.search();
          }
     }) 
	}
  }
};
</script>
<style>
.orderMain {width:90%;}
.upDiv {width:400px!important;}
</style>


